@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
:root {
  --marigold: #ffc001;

  --max-width: 1440px
}

html {
  color: white;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  background-color: black;
}

a {
  font-weight: 700;

}

button {
  cursor: pointer;
}
@-webkit-keyframes fade-in {
    from {
        opacity: 0.7;
    }

    to {
        opacity: 1;
    }
}

@keyframes fade-in {
    from {
        opacity: 0.7;
    }

    to {
        opacity: 1;
    }
}

.marquee-background {
    background-color: black;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    box-sizing: border-box;

    width: 100%;
    min-height: 100vh;
    padding: 0 9%;

    -webkit-animation-name: fade-in;

            animation-name: fade-in;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.marquee-container {
    position: relative;
    overflow: auto;

    width: 100%;
    max-width: var(--max-width);
    min-height: 100vh;
    margin: 0 auto;

    opacity: 1;
}

.main-content {
    height: 50%;
    padding-top: 38.5vh;
    padding-bottom: 5vh;
    margin-bottom: 22.1vh;

    display: grid;
    grid-template-columns: 51.9% 30.5%;
    grid-column-gap: 14.5%;
    -webkit-column-gap: 14.5%;
            column-gap: 14.5%;
    grid-template-rows: 100%;
}

.headline {
    display: inline-block;
    
    line-height: 5.25rem;
    font-size: 5.25rem;
    letter-spacing: -2.2px;

    padding: 0;
    margin: 0;

    grid-column: 1;
    grid-row: 1;
}

.subhead {
    display: inline-block;

    font-size: 0.875rem;
    line-height: 1.625rem;
    letter-spacing: 0;

    grid-column: 2;
    grid-row: 1;
}




.cta-container {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    background-color: white;
    color: black;

    box-sizing: border-box;
    
    width: 100%;
    height: 22.1vh;
    padding-left: 6.5%;
    bottom: 0;
    left: 0;
}

.cta-container h3 {
    font-size: 1.5rem;
    line-height: 2rem;
    letter-spacing: -0.63px;
    
    margin-right: auto;
}

.cta-btn {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: transparent;
    
    font-weight: 600;
    font-size: 0.688rem;
    line-height: 1.25rem;
    letter-spacing: 1px;
    
    
    border-top: none;
    border-right: none;
    border-bottom: 1px solid transparent;
    border-left: none;
    margin-right: auto;

    transition: 0.2s ease-out transform,
                0.3s ease-out border;
}

.cta-btn:focus {
    outline: none;
    border-bottom: 1px solid black;
}

.cta-btn:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.cta-btn:active {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

.arrow {
    color: var(--marigold);

    -webkit-transform: scale(1.3, 1);

            transform: scale(1.3, 1);
    height: 1.5vh;
    padding-left: 5px;

    transition: 0.2s ease-out transform;
}

.cta-btn:hover .arrow {
    -webkit-transform: translateX(8px) scale(1.3, 1);
            transform: translateX(8px) scale(1.3, 1);
}

.cta-btn:active .arrow {
    -webkit-transform: translateX(3px) scale(1.3, 1);
            transform: translateX(3px) scale(1.3, 1);
}



@media screen and (max-width: 850px) {
    .headline {
        font-size: 3.8rem;
        line-height: 4.2rem;
    }

    .cta-btn {
        margin-right: 10px;
    }
    
    .arrow {
        padding-left: 0px;
    }
    
}

@media screen and (max-width: 650px) {
    .main-content {
        display: block;
    }
    
    .headline {
        font-size: 3rem;
        line-height: 3.5rem;
    }
    
    .cta-container h3 {
        font-size: 1rem;
        line-height: 1.5rem;
    }
}


@media screen and (max-width: 450px) {
    
    .headline {
        font-size: 2rem;
        line-height: 3rem;
    }
    
    .cta-container h3 {
        font-size: 1rem;
        line-height: 1.5rem;
    }
}
header {
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 1;

    width: 82%;
    max-width: var(--max-width);
    padding: 2.3vh 9% 0 9%;
}

.header-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.nav-links {
    padding-top: 15px;
}

.nav-links a {
    display: block;
    color: white;

    font-size: 0.875rem;
    line-height: 1.625rem;
    letter-spacing: 0;

    text-decoration: none;

    transition: 0.3s ease-in-out color;
}

.nav-links a.selected {
    color: var(--marigold);
}

.contact-btn {
    background-color: transparent;
    color: white;
    box-sizing: border-box;

    font-weight: 700;
    font-size: 0.938rem;
    letter-spacing: 0;
    
    width: 11.8%;
    min-width: 100px;
    height: 5.2vh;
    border: 1px solid white;
    margin-top: 0.8vh;

    transition: 0.2s ease-out opacity;
    opacity: 0.9;
}

.contact-btn:focus {
    outline: 1px solid rgba(255, 255, 255, 0.37);
}

.contact-btn:hover {
    opacity: 1;
}

.contact-btn:active {
    opacity: 0.7;
}
